@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700,900);
:root{
    --color1: #C8DAE6;
    --color2: white;
    --color3: #B8B8B8;
    --color4: #FFCD41;
    --color5: #2092A9;
}

body {
    background-color: lighten(#C8DAE6, 10%);
    background-color: lighten(var(--color1), 10%);
}

*, *:before, *:after {
  box-sizing: border-box;
}

.weather-wrapper {
    position: relative;
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.weather-card {
    
    margin:  20px 5px;
    border-radius: 20px;
    position: relative;
    overflow: hidden;
    width: 270px;
    height: 270px;
    background-color: white;
    background-color: var(--color2);
    box-shadow: 0px 0px 25px 1px rgba(50, 50, 50, 0.1);
    -webkit-animation: appear 500ms ease-out forwards;
            animation: appear 500ms ease-out forwards;
}
    
.weather-card h1 {
    position: absolute;
    font-family: 'Lato', sans-serif;
    font-weight:300;
    font-size:80px;
    color: #B8B8B8;
    color: var(--color3);
    bottom: 0;
    left: 35px;
    opacity: 0;
    -webkit-transform: translateX(150px);
            transform: translateX(150px);
    -webkit-animation: title-appear 500ms ease-out 500ms forwards;
            animation: title-appear 500ms ease-out 500ms forwards;
}
    
.weather-card p {
    position: absolute;
    font-family: 'Lato', sans-serif;
    font-weight:300;
    font-size:28px;
    color: lighten(#B8B8B8, 10%);
    color: lighten(var(--color3), 10%);
    bottom: 0;
    left: 35px;
    -webkit-animation: title-appear 1s ease-out 500ms forwards;
            animation: title-appear 1s ease-out 500ms forwards;
}

.weather-icon {
    position: relative;
    width: 150px;
    height: 150px;
    top: 0;
    float: right;
    margin: 0px 0px 0 0;
    -webkit-animation: weather-icon-move 5s ease-in-out infinite;
            animation: weather-icon-move 5s ease-in-out infinite;
}

.weather-icon img {
    width: 150px;
}


@-webkit-keyframes light {
    from {
        box-shadow: rgba(255,255,0,0.2) 0 0 0 10px;
    }
    to {
        box-shadow: rgba(255,255,0,0.2) 0 0 0 17px;
    }
}


@keyframes light {
    from {
        box-shadow: rgba(255,255,0,0.2) 0 0 0 10px;
    }
    to {
        box-shadow: rgba(255,255,0,0.2) 0 0 0 17px;
    }
}

@-webkit-keyframes weather-icon-move {
	50% { 
        -webkit-transform: translateY(-8px); 
                transform: translateY(-8px); 
    }
}

@keyframes weather-icon-move {
	50% { 
        -webkit-transform: translateY(-8px); 
                transform: translateY(-8px); 
    }
}

@-webkit-keyframes appear {
  0% {
    -webkit-transform:scale(0);
            transform:scale(0);
  }
  50% {
    -webkit-transform:scale(1.05);
            transform:scale(1.05)
  }
  75% {
    -webkit-transform:scale(0.95);
            transform:scale(0.95)
  }
  100% {
   -webkit-transform:scale(1);
           transform:scale(1)
  }
}

@keyframes appear {
  0% {
    -webkit-transform:scale(0);
            transform:scale(0);
  }
  50% {
    -webkit-transform:scale(1.05);
            transform:scale(1.05)
  }
  75% {
    -webkit-transform:scale(0.95);
            transform:scale(0.95)
  }
  100% {
   -webkit-transform:scale(1);
           transform:scale(1)
  }
}

@-webkit-keyframes title-appear {
    from {
        opacity: 0;
        -webkit-transform: translateX(150px);
                transform: translateX(150px);
    }
    to {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes title-appear {
    from {
        opacity: 0;
        -webkit-transform: translateX(150px);
                transform: translateX(150px);
    }
    to {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

.header-content{
    height: 100%;
}

.header-nav-wrap{
    height: 100%;
}

ul.header-nav {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: skyblue;
}

ul.header-nav li {
    float: left;
}

ul.header-nav li button {
    display: block;
    color: white;
    font-size: 12px;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    background-color: transparent;
    border: none;
    cursor: pointer;
    margin: 0;
    width: 100%;
}

.search-container {
    float: right;
}
  
.search-container input[type=text] {
    padding: 6px;
    margin-top: 8px;
    font-size: 12px;
    border: none;
}
  
.search-container button {
    float: right;
    padding: 6px 10px;
    margin-top: 8px;
    margin-right: 16px;
    background: #ddd;
    font-size: 12px;
    border: none;
    cursor: pointer;
}
  
.search-container button:hover {
    background: #ccc;
}
  
/* Change the link color to #111 (black) on hover */
ul.header-nav li button.active {
    background-color: #FFCD41;
    background-color: var(--color4);
}

ul.header-nav li button:hover:not(.active){
    background-color: #FFCD41;
    background-color: var(--color4);
}

ul.header-nav li.right {float: right;}

@media screen and (max-width: 600px) {
  ul.header-nav li.right, 
  ul.header-nav li {float: none;}
  .search-container {
    float: none;
  }
  .search-container input {
      width: 100%;
  }
  .search-container button {
    width: 100%;
    display: block;
    margin: 0;
  }
}

.weather-large {
    margin-top: 200px;
    margin-left: 50px;
    margin-right: 50px;
    position: relative;
}

.weather-header h1{
    margin-top: 20px;
    position: relative;
    font-size: 25px;

}


